$heeboFont: "Heebo", open sans-serif;
$primary500: #1A15EF;
$primary800: #0A087A;
$tokiWhite: #F3F3F3;
$h2Size: 1.425em;
$h3Size:1.269em;
$subtitleSize: 1.125em;
$bodySize: 1em;
$captionSize: 0.888em;

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    background-color: $tokiWhite;
    font-family: $heeboFont;
}

body{
    padding: 0% 5% 0% 5%;
}

.flex {
    display: flex;
    align-items: center;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}




.nav{
    background-color: $tokiWhite;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;

    &__list{
        justify-content: center;
        display: flex;
        justify-content: space-evenly;
        padding-top: 0.75em;
    }
}